@font-face {
    font-family: 'dearquest';
    src: url('../../fonts/dearquest.woff') format('woff');
    font-weight: normal;
    font-style: normal;
  }
  
  @font-face {
    font-family: 'yours';
    src: url('../../fonts/yours.woff') format('woff');
    font-weight: normal;
    font-style: normal;
  }
  

    
  @font-face {
    font-family: 'weareweating';
    src: url('../../fonts/weareweating.woff') format('woff');
    font-weight: normal;
    font-style: normal;
  }

  .backgroind-img{
    position: absolute;
    width: 100%;
    height: 100%;
    z-index: -2;
    background-image:  url('../title/title.png');
    background-position: 50% 50%;
    background-size: cover;
    background-repeat: no-repeat;
  }
  

.info-container {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-top: 50px;

}

.info-dearquest {
    font-size: 50pt;
    font-family: dearquest;
    color: rgb(0, 0, 0);
    text-align: center;
    line-height: 1.25em;
    margin-top: 10px;
    margin-bottom: 10px;
}

.info-main-info {
    font-size: 11.9903pt;
    font-family: "Calme Extra";
    color: rgb(0, 0, 0);
    text-align: center;
}

.info-wearewaitingyou {
    font-size: 56pt;
    line-height: 1.25em;
    letter-spacing: 0em;
    text-align: center;
    font-family: weareweating;
    color: rgb(0, 0, 0) !important;
    margin-top: 10px;
    margin-bottom: 10px;
}

.info-maindata {
    font-size: 10.2774pt;
    font-family: "Calme Extra";
    color: rgb(0, 0, 0);
    text-align: center;
}

.info-yours {
    font-size: 44pt;
    font-family: yours;
    color: rgb(0, 0, 0);
    text-align: center;
    margin-top: 10px;
    margin-bottom: 10px;
}


.confirmation-wrapper {
    padding: 30px 20px 20px 20px;
    border-radius: 7px;
    color: #3c763d;
    background-color: #dff0d8;
    border-color: #d6e9c6;
    margin: auto;
    max-width: 300px;
    width: 100%;
    position: relative;
    font-family: Rotonda;
    margin-top: 25px;
    margin-bottom: 60px;
    min-height: 106px;
    display: flex;
    justify-content: space-between;
    flex-direction: column;
}



.confirmation-text {
    margin: auto;
    max-width: 218px;
    width: 100%;
    position: relative;
    font-family: Rotonda;
    color: #3c763d;
    background-color: #dff0d8;
    border-color: #d6e9c6;
}

.confirmation-btn {
    margin: 15px auto;
    width: 100%;
    font-family: 'Montserrat';
    font-size: 1.12em;
    font-weight: 600;
    font-size: 1.12em;
    color: #333;
    background-color: #fff;
    border-color: #ccc;
    display: inline-block;
    margin-bottom: 0;
    font-size: 14px;
    font-weight: 400;
    line-height: 1.42857143;
    text-align: center;
    white-space: nowrap;
    vertical-align: middle;
    -ms-touch-action: manipulation;
    touch-action: manipulation;
    cursor: pointer;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    background-image: none;
    border: 1px solid transparent;
    border-radius: 4px;
    height: 33px;
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;

}

.OkIMg {
    height: 50px;
    width: 50px;
}
