        font-family: weareweating;
        color: rgb(0, 0, 0) !important;
        margin-top: 10px;
        margin-bottom: 10px;
    }

    
    .info-yours {
        font-size: 33pt;
        font-family: yours;
        color: rgb(0, 0, 0);
