.map-container {
    display: flex;
    align-items: center;
    flex-direction: column;
    position: relative;
    width: 100%;
    height: 520px;
}

img.leaflet-marker-icon
{
    background-image: url("data:image/png;base64,");
}

/* .leaflet-tile-container img {
    box-shadow: 0 0 1px rgba(0, 0, 0, 0.05);
} */

/* .leaflet-tile-container img {
    width: 257px !important;
    height: 257px !important;
  } */


  img.leaflet-tile, img.leaflet-marker-icon, img.leaflet-marker-shadow {
    /* work-around from here: https://github.com/Leaflet/Leaflet/issues/161 */
    outline: 1px solid transparent;
    /* work-around from here: https://bugs.chromium.org/p/chromium/issues/detail?id=600120 */
    mix-blend-mode: plus-lighter;
}

.leaflet-tile-container img {
    width: 256px !important;
    height: 256px !important;
  }


  .map-main-place {
    display: flex;
    /* font-family: 'dearquest'; */
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-size: 13pt;
    text-align: center;
    margin-bottom: 10px;
  }

  .zagc {
    font-size: 13pt;
    line-height: 1.25em;
    font-family: 'maintitlefont', sans-serif;
    margin-bottom: 13px;
  }