.object {
  transform: scale(0.5); /* начальный масштаб объекта */
  animation-name: zoom; /* название анимации */
  animation-duration: 1s; /* длительность анимации */
  animation-timing-function: ease-in-out; /* функция времени для плавности */
  animation-fill-mode: forwards; /* сохраняем состояние анимации после ее завершения */
}

@keyframes zoom {
  from {
    transform: scale(0.5); /* начальный масштаб объекта */
  }
  to {
    transform: scale(1); /* конечный масштаб объекта */
  }
}


.object2 {
  transform: scale(1.5); /* начальный масштаб объекта */
  animation-name: zoom2; /* название анимации */
  animation-duration: 1s; /* длительность анимации */
  animation-timing-function: ease-in-out; /* функция времени для плавности */
  animation-fill-mode: forwards; /* сохраняем состояние анимации после ее завершения */
}

@keyframes zoom2 {
  from {
    transform: scale(1.5); /* начальный масштаб объекта */
  }
  to {
    transform: scale(1); /* конечный масштаб объекта */
  }
}