.questions-container {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.question-label {
    margin-top: 15px;
    margin-bottom: 15px;
}

.questions-block {
    border-radius: 8px;
    padding: 0 20px 20px;
    width: 50%
}


@media (max-width: 420px) {

    .questions-block {
        border-radius: 8px;
        padding: 0 20px 20px;
        width: 100%
    }

    .questions-container .white-background {
        display: flex;
        flex-direction: column;
        justify-content: left !important;
        align-items: start !important;
        background-color: rgb(253 245 254 / 60%);
        border-radius: 13px;
        width: 85%;
    }
}


.question-btn {
    margin: 15px auto;
    width: 300px;
    font-family: 'Montserrat';
    font-size: 1.12em;
    font-weight: 600;
    font-size: 1.12em;
    color: #333;
    background-color: #fff;
    border-color: #ccc;
    display: inline-block;
    margin-bottom: 0;
    font-size: 18px;
    font-weight: 400;
    line-height: 1.42857143;
    text-align: center;
    white-space: nowrap;
    vertical-align: middle;
    touch-action: manipulation;
    cursor: pointer;
    -webkit-user-select: none;
    user-select: none;
    background-image: none;
    border: 1px solid transparent;
    border-radius: 4px;
    height: 33px;
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #dff0d8;
    border-color: #d6e9c6;
    margin-bottom: 25px;
}