.timeline-container {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 775px;

}

.timeline-header {
    font-size: 50pt;
    font-family: 'dearquest';
    margin: 35px 0;
    line-height: 1.25em;
    text-align: center;
}

.ant-timeline-item {
    position: relative;
    margin: 0;
    padding-bottom: 20px;
    font-size: 17px !important;
    list-style: none;
}

.white-background {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: rgb(253 245 254 / 60%);
    border-radius: 13px;
    margin: 20px 0;
    width: 60%;
}

.timeline-item-container {
    width: 50%;
}

@media (max-width: 420px) {
    .timeline-container {
        position: relative;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        height: 1120px;

    }

    .timeline-header {
        font-size: 50pt;
        font-family: 'dearquest';
        margin: 35px 0;
        line-height: 1.25em;
        text-align: center;
    }

    .timeline-item-container {
        width: 80%;
    }

    .white-background {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        background-color: rgb(253 245 254 / 60%);
        border-radius: 13px;
        width: 95%;

    }
}

@media (min-width: 420px) and (max-width: 690px) {
    .timeline-container {
        position: relative;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        height: 1077px;

    }

    .timeline-item-container {
        width: 76%;
    }

    .white-background {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        background-color: rgb(253 245 254 / 60%);
        border-radius: 13px;
        /* margin: 20px 0; */
        width: 85%;
    }
}

@media (min-width: 690px) and (max-width: 1200px) {
    .timeline-container {
        position: relative;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        height: 1020px;

    }


    .white-background {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        background-color: rgb(253 245 254 / 60%);
        border-radius: 13px;
        /* margin: 20px 0; */
        width: 85%;
    }
}